.cell{
    height:50px;
    width:50px;
    background-color: white;
    outline:1px solid rgb(175, 216, 248);
    display: inline-block;
    font-size: 25px;
    text-align: center;
    color: black;
    border-radius: 5px 20px;
}

.cell-prime{
    background-color: #00f51d;
    -webkit-animation-name: isPrime;
            animation-name: isPrime;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;

}
@-webkit-keyframes isPrime {
    0% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        background-color: rgba(0, 0, 66, 0.75);
        border-radius: 100%;
    }

    50% {
        background-color: rgba(17, 104, 217, 0.75);
        -webkit-transform: scale(1.5);
                transform: scale(1.5);

    }

    75% {
        background-color: rgba(0, 217, 159, 0.75);
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);

    }

    100% {

        background-color: rgba(0, 190, 218, 0.75);
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes isPrime {
    0% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        background-color: rgba(0, 0, 66, 0.75);
        border-radius: 100%;
    }

    50% {
        background-color: rgba(17, 104, 217, 0.75);
        -webkit-transform: scale(1.5);
                transform: scale(1.5);

    }

    75% {
        background-color: rgba(0, 217, 159, 0.75);
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);

    }

    100% {

        background-color: rgba(0, 190, 218, 0.75);
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.cell-check{
    background-color: grey;
}
.cell-visiting{
    outline:3px solid rgb(175, 216, 248);
    -webkit-animation-name: isVisiting;
            animation-name: isVisiting;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}
@-webkit-keyframes isVisiting {
    50%{
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@keyframes isVisiting {
    50%{
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
