* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Piazzolla", serif;
}

:root {
    --main-bg: #1e272e;
    --main-color: #0097e6;
}

body {
    height: 90vh;
    display: flex;
    flex-direction: column;
}

.row {
    margin: 0;
}

/* main-header */
.main_header{
    background-color: rgb(255, 255, 255);
    
}
.main_header_left {
    height: 28rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.main_header_left p {
    font-size: 1.5rem;
}

.main_header_left h1 {
    font-size: 3rem;
    margin: 1rem 0 4rem 0;
    text-transform: capitalize;
}

.main_header_left button {
    border: none;
    padding: 0.8rem 1.8rem;
    font-size: 1.4rem;
    border-radius: 2rem;
    text-transform: uppercase;
    box-shadow: 0 8px 6px -6px #000;
    outline: none;
}

.main_header_left button:hover {
    box-shadow: 0 8px 6px -6px #0097e6;
    box-shadow: 0 8px 6px -6px var(--main-color);
}

.main_header_left .text_clr {
    color: #0097e6;
    color: var(--main-color);
}

.main_header_right {
    height: 28rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.main_header_right figure img {
    width: 20rem;
    height: 20rem;
    -webkit-filter: drop-shadow(0 0.5rem 1rem rgba(0, 0, 0, 0.3));
            filter: drop-shadow(0 0.5rem 1rem rgba(0, 0, 0, 0.3));
}

/* responsive code */

@media(max-width:768px) {
    .main_header_left {
        height: auto;
        margin: 5rem 0;
    }
    .main_header_right {
        height: auto;
        margin-top: 3rem;
    }
}

